var imageActions = require('../../actions/images');
var objectActions = require('../../actions/objects');
var videoActions = require('../../actions/videos');
var oembedActions = require('../../actions/oembeds');
var subdocActions = require('../../actions/subdocs');

export default function initObjects(quill, docId) {
  // Clicking again on a selected object iframe will trigger its edit interface
  quill.root.addEventListener('click', e => {
    var iframe = e.target;
    if (
      iframe.tagName === 'IFRAME' && iframe.contentWindow.document.body &&
      iframe.contentWindow.document.body.classList.contains('is-active')
    ) {
      var data = JSON.parse(iframe.dataset.object);
      var target = { docId, lineId: findClosestId(iframe.parentNode, quill.root) };

      if (
        ('hr' in data) ||
        ('newsletter' in data && app.state.get(['community', 'newsletters']).length <= 1)
      ) {
        return;
      }

      if ('image' in data) {
        imageActions.getAndEditImage(data.image, 'image_inline', target);
      } else if ('video' in data) {
        videoActions.getAndEditVideo(data.video, 'Update', target);
      } else if ('oembed' in data) {
        oembedActions.editOembed(data.oembed, 'Update', target);
      } else if ('doc' in data) {
        subdocActions.editDoc(data.doc.id, 'Update', target);
      } else {
        objectActions.editObject(data, 'Update', target);
      }
    }
  }, true);
}

function findClosestId(node, stopAt) {
  while (node && node !== stopAt) {
    if (node.id) {
      return node.id;
    } else {
      node = node.parentNode;
    }
  }
  return null;
}
