/**
 * Similar to jQuery's $(el).closest(selector)
 *
 * Return the first element found to match the selector, starting with the
 * element itself, then traversing up through the element's ancestors.
 *
 * If neither the element nor any of its parent elements match the selector,
 * null is returned.
 *
 * Is commonly used to perform event delegation.
 *
 * @param {HTMLElement} el - The element to start searching from.
 * @param {string} selector - A selector expression to match elements against.
 * @returns {?HTMLElement}
 */
module.exports = function closest(el, selector) {
  while (el && typeof el.matches === 'function') {
    if (el.matches(selector)) {
      return el;
    } else {
      el = el.parentNode;
    }
  }
  return null;
};
