var autosave = require('../../actions/autosave');

export default function initAutosave(component, quill) {
  // Autosave events
  component.listenTo(app, 'autosave.remote-update', (docId, packet) => component.update(docId, packet.delta));
  component.listenTo(app, 'autosave.local-update', component.update);
  component.listenTo(app, 'autosave.check-update', (docId) => {
    if (docId === component.get('docId')) {
      quill.update();
    }
  });

  // Quill text-change
  component.listenTo(quill, 'text-change', component.handleTextChange);

  // On load, if ops.length === 0, this is a new doc. We need to send an
  // autosave packet with the initial newline that Quill inserts by default
  if (component.getValue().ops.length === 0) {
    var contents = quill.getContents();
    autosave.localUpdate(component.get('docId'), [{
      p: component.get('path'),
      t: 'rich-text',
      o: contents
    }], -1);
  }
}
