// When hitting 'Enter' at the end of a paragraph formatted as a header,
// the next line should revert to standard paragraph formatting

import { Delta } from 'rich-text';

const REMOVE_HEADERS = {
  firstheader: null,
  secondheader: null,
  thirdheader: null,
  fourthheader: null,
  fifthheader: null
};

export default {
  key: 'Enter',
  collapsed: true,
  format: Object.keys(REMOVE_HEADERS),
  suffix: /^$/,
  handler(range, context) {
    let [line, offset] = this.quill.getLine(range.index);
    let delta = new Delta()
      .retain(range.index)
      .insert('\n', context.format)
      .retain(line.length() - offset - 1)
      .retain(1, REMOVE_HEADERS);
    this.quill.updateContents(delta, 'user');
    this.quill.setSelection(range.index + 1, 'silent');
    this.quill.scrollIntoView();
  }
};
