import { Delta } from 'rich-text';

export default {
  key: '-',
  collapsed: true,
  prefix: /-$/,
  handler(range, context) {
    const delta = new Delta()
      .retain(range.index - 1)
      .delete(2)
      .insert('—', context.format);

    // Insert the dash normally, for undo
    this.quill.insertText(range.index, '-', context.format, 'user');

    // Apply the delta
    this.quill.history.cutoff();
    this.quill.updateContents(delta, 'user');
    this.quill.history.cutoff();

    // Manually change the selection upon undo/redo
    this.quill.setSelection(range.index, 0, 'silent');
    this.quill.history.recordSelection(range.index + 1, range.index);

    // Register backspace as undo shortcut
    this.quill.history.undoOnBackspace();
  }
};
