import { Delta } from 'rich-text';

export default function block(pattern, formatName, options) {
  return Object.assign({
    key: ' ',
    collapsed: true,
    prefix: pattern,
    handler(range, context) {
      // Abort if the format is not whitelisted
      if (!this.quill.scroll.whitelist[formatName]) {
        return true;
      }

      // Build delta that deletes the shortcut and reformats the line
      const [line, offset] = this.quill.scroll.line(range.index);
      const delta = new Delta()
        .retain(range.index - offset)
        .delete(offset + 1)
        .retain(line.length() - offset - 1)
        .retain(1, { [formatName]: true });

      // Insert space for undo
      this.quill.insertText(range.index, ' ', context.format, 'user');

      // Apply delta
      this.quill.history.cutoff();
      this.quill.updateContents(delta, 'user');
      this.quill.history.cutoff();

      // Record position in undo history
      this.quill.history.recordSelection(range.index + 1, range.index - 1);

      // Register backspace as undo shortcut
      this.quill.history.undoOnBackspace();

      return false;
    }
  }, options);
}
