import { Delta } from 'rich-text';

const VALID_FRACTIONS = ['1/2', '1/3', '1/4', '2/3', '3/4'];
const PATTERN = /(^|\s)-?([123]\/[234])$/;

export default {
  key: ' ',
  collapsed: true,
  prefix: PATTERN,
  handler(range, context) {
    const match = context.prefix.match(PATTERN);
    const fraction = match[2];

    if (VALID_FRACTIONS.includes(fraction)) {
      const delta = new Delta()
        .retain(range.index - fraction.length)
        .retain(1, { superscript: true })
        .delete(1)
        .insert('\u2044', context.format)
        .retain(1, { subscript: true });

      // Insert the space normally, for undo
      this.quill.insertText(range.index, ' ', context.format, 'user');

      // Apply the delta
      this.quill.history.cutoff();
      this.quill.updateContents(delta, 'user');
      this.quill.history.cutoff();

      // Manually fix the cursor to come after the space, and also upon undo/redo
      this.quill.setSelection(range.index + 1, 0, 'silent');
      this.quill.history.recordSelection(range.index + 1);

      // Register backspace as undo shortcut
      this.quill.history.undoOnBackspace();
      return false;
    }

    return true;
  }
};
