const FORMATS = [
  'list',
  'bullet',
  'blockquote'
];

export default {
  key: ['Backspace', 'Enter'],
  shiftKey: null,
  metaKey: null,
  ctrlKey: null,
  altKey: null,
  collapsed: true,
  offset: 0,
  format: FORMATS,
  handler(range, context) {
    let preventDefault = false;

    for (let format of FORMATS) {
      if (context.format[format]) {
        this.quill.format(format, false, 'user');
        preventDefault = true;
      }
    }

    return !preventDefault;
  }
};
