import { Delta } from 'rich-text';
import { escapeRegExp } from 'lodash';

export default function inline(char, formatName, options) {
  const escapedChar = escapeRegExp(char);
  const edge = `[^\\s${escapedChar}]`;
  const between = `[^${escapedChar}]*`;
  const wrappable = `(${edge}|${edge}${between}${edge})`;
  const pattern = new RegExp(`(^|\\s)(${escapedChar}${wrappable})$`);

  return Object.assign({
    key: char,
    collapsed: true,
    prefix: pattern,
    handler(range, context) {
      // Abort if the format is not whitelisted
      if (!this.quill.scroll.whitelist[formatName]) {
        return true;
      }

      // Abort if there an even number of the trigger char on this line
      if (context.prefix.split(char).length % 2 !== 0) {
        return true;
      }

      // Insert the char, for undo
      this.quill.insertText(range.index, char, context.format, 'user');

      // Build a delta that applies the format and removes the fenceposts
      const wrapLength = context.prefix.length - context.prefix.lastIndexOf(char) - 1;
      const delta = new Delta()
        .retain(range.index - wrapLength - 1)
        .delete(1)
        .retain(wrapLength, { [formatName]: true })
        .delete(1);

      // Apply the delta
      this.quill.history.cutoff();
      this.quill.updateContents(delta, 'user');
      this.quill.history.cutoff();

      // Manually fix selection for the removed character
      this.quill.setSelection(range.index - 1, 0, 'user');

      // Record the selection so it is applied on undo/redo
      this.quill.history.recordSelection(range.index + 1, range.index - 1);

      // Register backspace as undo shortcut
      this.quill.history.undoOnBackspace();

      // Turn the applied format back off
      this.quill.format(formatName, false);

      return false;
    }
  }, options);
}
