import { Delta } from 'rich-text';

// Matches URLs with optional preceeding link text separated by colon, e.g:
//   Vox:vox.com
//   "The Verge Podcasts":https://theverge.com/podcasts

// Comments and whitespace are stripped to turn into a real RegExp
// Compiled version at: http://rubular.com/r/QPbcWUWuqi
const LINK_PATTERN = new RegExp(`
  # Preceeding space, or beginning of line (capture group 1)
  (^|\\s)

  # Link text + colon separator (optional), e.g. <"my link text":>
  (?:
    # The link text itself, either:
    (?:
      ([^\\s"“”]+)         # A run of non-whitespace, non-quote characters (capture group 2)
      |
      ["“”]([^"“”]+)["“”]  # Anything between quote marks (capture group 3)
    )

    # Separator
    :
  )?

  # URL (capture group 4)
  (
    (https?:\\/\\/)?      # Protocol (optional, capture group 5)
    (?:[\\w-]+\\.)+       # Subdomains
    ([\\w-]+)             # TLD (capture group 6)
    (?::\\d+)?            # Port (optional)
    (?:\\/[^\\s:]*?)?     # Path starting with a / (optional)
  )

  # Trailing punctuation (optional)
  (?:[.,'’"”:)])?

  # Anchored to end
  $
`.replace(/(\s+|#.*?\n)/g, ''));

const TLD_SET = new Set(['com', 'net', 'org', 'gov', 'co', 'uk', 'us', 'ca', 'eu', 'biz', 'info', 'io', 'ly']);

export default {
  key: ['Enter', ' '],
  collapsed: true,
  shiftKey: null,
  prefix: LINK_PATTERN,
  handler(range, context) {
    if (!this.quill.scroll.whitelist.link) {
      return true;
    }

    const {
      0: matchText,
      1: preSpace,
      2: bareLinkText,
      3: quotedLinkText,
      4: url,
      5: protocol,
      6: tld
    } = context.prefix.match(LINK_PATTERN);

    if (!TLD_SET.has(tld)) {
      return true;
    }

    // Prepend protocol if necessary
    const href = protocol ? url : `http://${url}`;

    // Make a delta the formats the matched text with the url
    const delta = new Delta()
      .retain(range.index - matchText.length + preSpace.length);

    if (bareLinkText) {
      delta.retain(bareLinkText.length, { link: href });
      delta.delete(1 + url.length); // The separator colon and url
    } else if (quotedLinkText) {
      delta.delete(1); // The opening quote
      delta.retain(quotedLinkText.length, { link: href });
      delta.delete(2 + url.length); // The closing quote, separator colon, and url
    } else {
      delta.retain(url.length, { link: href });
    }

    // Allow the default behavior to happen first
    this.quill.once('debounced-text-change', () => {
      // Apply the delta
      this.quill.history.cutoff();
      this.quill.updateContents(delta, 'user');
      this.quill.history.cutoff();

      // Ensure the selection remains unchanged upon undo/redo
      this.quill.history.recordSelection(range.index + 1);

      // Register backspace as undo shortcut
      this.quill.history.undoOnBackspace();
    });

    return true;
  }
};
