import { Delta } from 'rich-text';

const OPEN_PUNCTUATION = /(^|[\s{[(<'"“‘])$/;

export default {
  key: ['"', '\''],
  shiftKey: null,
  handler(range, context) {
    let prevText = this.quill.getText(range.index - 1, 1);
    let char;
    if (context.event.key === '"') {
      char = OPEN_PUNCTUATION.test(prevText) ? '“' : '”';
    } else {
      char = OPEN_PUNCTUATION.test(prevText) ? '‘' : '’';
    }
    let delta = new Delta()
      .retain(range.index)
      .delete(range.length)
      .insert(char, context.format);
    this.quill.updateContents(delta, 'user');
    this.quill.setSelection(range.index + 1, 0, 'silent');
  }
};
