// Normally when a line is split, both the original and new lines are formatted
// exactly the same. For some attributes, we want only the old line to keep the
// relevant format, and to strip it from the new one (or vice versa).

const REMOVE_FROM_BEFORE_SPLIT = {
  br: false,
  endmark: false
};

const REMOVE_FROM_AFTER_SPLIT = {
  dropcap: false,
  leadgraf: false,
  position: false
};

export default {
  key: 'Enter',
  shiftKey: false,
  format: [
    ...Object.keys(REMOVE_FROM_BEFORE_SPLIT),
    ...Object.keys(REMOVE_FROM_AFTER_SPLIT)
  ],
  handler(range) {
    // Allow the enter key to split a line normally, then remove formats
    Promise.resolve().then(() => {
      this.quill.formatLine(range.index, 1, REMOVE_FROM_BEFORE_SPLIT, 'user');
      this.quill.formatLine(range.index + 1, 1, REMOVE_FROM_AFTER_SPLIT, 'user');
    });
    return true;
  }
};
