var Delta = require('rich-text').Delta;
var { throttle, isEqual } = require('lodash');
var getColor = require('../../lib/get_color');

const CURSOR_COLORS = [
  '#BC1B92',
  '#406EE2',
  '#68A1B5',
  '#DCA427',
  '#E04D20'
];

export default function initMultiCursor(component, quill, path, id) {
  var cursorManager = quill.theme.addModule('multi-cursor', true);
  var docId = component.get('docId');
  var room = `${id}.${path}`;
  var socket = app.socket;
  var pendingDelta = new Delta();

  // Connection logic
  socket.emit('cursors.connect', room);
  socket.on('reconnect', () => socket.emit('cursors.connect', room));
  component.on('teardown', () => socket.emit(`cursors.${room}.leave`));

  // Compose incoming packets that apply to the current document
  component.listenTo(app, `autosave.dirty.${docId}`, (inLine, inFlight) => {
    if (inLine) {
      pendingDelta = composeDelta(pendingDelta, inLine.delta, path);
    }
    if (inFlight) {
      pendingDelta = composeDelta(pendingDelta, inFlight.delta, path);
    }
  });

  // Reset the pending delta whenever autosave resolves
  component.listenTo(app, `autosave.clean.${docId}`, () => {
    pendingDelta = new Delta();
  });

  function sendLocation() {
    var range = quill.getSelection();
    var index = range ? range.index + range.length : null;
    socket.emit(`cursors.${room}.change`, index);
  }

  component.listenTo(quill, 'selection-change', throttle(sendLocation, 500));
  component.listenTo(app, `autosave.success.${docId}`, sendLocation);

  // Move cursor based on server-side data
  component.listenTo(socket, `cursors.${room}.change`, (socketId, userId, index) => {
    if (index != null) {
      var user = app.state.get(['users']).find(({ id }) => id === userId);
      var color = getColor(CURSOR_COLORS, socketId);
      index = pendingDelta.transformPosition(index);
      cursorManager.setCursor(userId, index, user.fullOrUserName, color);
    } else {
      cursorManager.removeCursor(userId);
    }
  });
}

function composeDelta(pendingDelta, delta, path) {
  for (let op of delta) {
    if (isEqual(op.p, path) && op.t === 'rich-text') {
      pendingDelta = pendingDelta.compose(op.o);
    }
  }

  return pendingDelta;
}
