import block from './_block';

const BINDINGS = {
  1: block(/^#$/, 'firstheader'),
  2: block(/^##$/, 'secondheader'),
  3: block(/^###$/, 'thirdheader'),
  4: block(/^####$/, 'fourthheader'),
  5: block(/^#####$/, 'fifthheader')
};

export default {
  key: ' ',
  collapsed: true,
  prefix: /^#{1,5}$/,
  handler(range, context) {
    return BINDINGS[context.offset].handler.call(this, range, context);
  }
};
